import React from "react"
import * as shellDecoratorStyle from "./shelldecorator.module.css"

class ShellDecorator extends React.Component {

    render() {
        const children = this.props.children
        const autoSize = this.props.autoSize

        const defaultStyle = shellDecoratorStyle.shellDecorator

        const compoundStyle = autoSize ? defaultStyle + " " + shellDecoratorStyle.autoSize : defaultStyle

        return (
            <div className={ compoundStyle }>
                { children }
                <div className={ shellDecoratorStyle.macWindowButtons }></div>
            </div>
        )
    }
}

ShellDecorator.defaultProps = {
    autoSize: false
}

export default ShellDecorator