import React from "react"
import Prism from "prismjs"
import "prismjs/themes/prism-tomorrow.css"
import { Button } from "react-bootstrap"
import * as clipboard from "clipboard-polyfill"
import * as codeBlockStyle from "./codeblock.module.css"

class Codeblock extends React.Component {

    componentDidMount() {
      Prism.highlightAll();
    }

    handleCopyBtnClick() {
        clipboard.writeText(this.props.children.toString());
    }

    render() {
        const languageCssClass = "language-" + this.props.language
        const children = this.props.children
        const copyBtn = this.props.copyBtn

        return (
            <pre className={ languageCssClass + " " + codeBlockStyle.adjustPre }>
                <code className={ languageCssClass }>
                    { children }
                </code>
                { copyBtn &&
                    <div className={ codeBlockStyle.copyBtnContainer }>
                        <Button variant="outline-primary" size="sm" onClick={ () => this.handleCopyBtnClick() }>Copy</Button>
                    </div>
                }
            </pre>
        )
    }
}

Codeblock.defaultProps = {
    copyBtn: true
}

export default Codeblock